import React, {Component} from 'react';
import {Card, CardBody, CardHeader, CardTitle, Col, Container, Input, Nav, Row} from 'reactstrap';
import 'react-table/react-table.css'
import SweetAlert from 'react-bootstrap-sweetalert';
import Notify from 'react-notification-alert';
import 'react-datetime/css/react-datetime.css'
import 'moment/locale/tr'
import StickButton from "../../../components/StickButton";
import {requestK} from "../../../helper/APIUtils";
import {showAxiosError} from "../../../helper/helper";
import Alert from "react-s-alert";
import Table from "../../../components/Table/Table";
import TextOverFlowTooltip from "../../../components/TextOverFlowTooltip";
import DocumentState from "../common/DocumentState";
import {renewListForPrivileges} from "../common/PeykHelper";

class FormResendPage extends Component {

  constructor(props) {
    super(props);
    this.state = {
      loading: false,
      completed: false,
      signedDocumentCount: 0,
      progressLength: 1,
      timeoutList: [],
      page: 0,
      size: 10,
      total: 0,
      content: [],
      manual: false,
    };
    this.notifyMimeResend = React.createRef();

    this.resend = this.resend.bind(this);
    this.fetchData = this.fetchData.bind(this);
    this.readMimeInfo = this.readMimeInfo.bind(this);
    this.checkTimeouts = this.checkTimeouts.bind(this);
    this.showErrorMessage = this.showErrorMessage.bind(this);
  }

  showErrorMessage(error) {
    this.notifyMimeResend.current.notificationAlert({
      place: 'tr',
      message: 'Hata: ' + error.errorMessage,
      type: 'danger',
      autoDismiss: 10,
    });
  }

  componentDidMount() {
    if (!this.props || !this.props.location || !this.props.location.session)
      this.props.history.push({pathname: `/${this.props.peykName}/formsessionlist`});
    else {
      this.checkTimeouts();
      this.readMimeInfo();
      this.readSmsTemplate();
      this.readMailTemplate();
    }
  }

  readSmsTemplate = () => {
    let self = this;
    let params = {
      url: `/signer/readFormSmsTemplateInfo`,
      method: "get"
    };
    requestK(params)
      .then(function (response) {
        self.setState({smsBody: response.data})
      }).catch(function (error) {
      showAxiosError(error);
    });
  };

  readMailTemplate = () => {
    let self = this;
    let params = {
      url: `/signer/readFormMailTemplateInfo`,
      method: "get"
    };
    requestK(params)
      .then(function (response) {
        if (response.data) {
          self.setState({mimeBody: response.data})
        }
      }).catch(function (error) {
      showAxiosError(error);
    });
  };

  checkTimeouts() {
    let self = this;
    let params = {
      url: '/formsession/readTimeouts',
      method: "post",
      data: self.props.location.session.id
    };
    self.setState({loading: true});
    requestK(params)
      .then(function (response) {
        if (response.data && response.data.length > 0) {
          self.setState({timeoutList: response.data});
        } else {
          Alert.error("Zaman aşımına düşmüş doküman bulunmamaktadır.", {
            position: 'top-right',
            effect: 'stackslide',
            timeout: 5000
          });
          self.props.history.push({pathname: `/${self.props.peykName}/formsession/` + self.props.location.session.id});
        }
        self.setState({loading: false});
      })
      .catch(function (error) {
        showAxiosError(error);
        self.setState({loading: false});
      });
  }

  readMimeInfo() {
    let self = this;
    let params = {
      url: '/formsession/readMimeInfo',
      method: "post",
      data: self.props.location.session.id
    };
    self.setState({loading: true});
    requestK(params)
      .then(function (response) {
        self.setState({
          mimeTitle: response.data.mimeTitle,
          mimeBody: response.data.mimeBody,
          loading: false,
        });
        if (self.props.location.isKepOwner) {
          self.setState({kepAccount: response.data.account});
        }
      })
      .catch(function (error) {
        showAxiosError(error);
        self.setState({loading: false});
      });
  }

  fetchData(accessor, direction, page, size) {
    let self = this;
    let sortStr = accessor !== undefined ? ('&sort=' + accessor + ',' + direction) : '';
    let params = {
      url: '/form/readSelectedFormsBySession?page=' + page + '&size=' + size + sortStr,
      method: "post",
      data: self.state.timeoutList
    };
    self.setState({loading: true, manual: self.manual === true ? false : self.manual});
    requestK(params)
      .then(function (response) {
        self.setState({
          page: page,
          size: size,
          total: response.data.totalElements,
          content: response.data.content,
          loading: false
        });
      }).catch(function (error) {
      self.setState({loading: false});
      showAxiosError(error);
    });
  }


  resend() {
    let self = this;
    if (self.state.timeoutList && self.state.timeoutList.length > 0) {
      let params = {
        url: `/formsession/resend`,
        method: "post",
        data: self.state.timeoutList
      };
      self.setState({disableSendBtn: true});
      requestK(params)
        .then(function (response) {
          Alert.success("Dokümanlar tekrar gönderim için işleme alındı.", {
            position: 'top-right',
            effect: 'stackslide',
            timeout: 2000
          });
          self.setState({disableSendBtn: false});
          self.props.history.push({pathname: `/${self.props.peykName}/formsession/` + self.props.location.session.id});
        })
        .catch(function (error) {
          showAxiosError({message: "Şu an işleminiz gerçekleştirilemiyor."});
          self.setState({disableSendBtn: false});
        });
    } else {
      showAxiosError({message: "Zaman aşımına uğramış liste algılanamadı. Lütfen bu sayfadan çıkıp yapıp tekrar deneyiniz."});
    }
  }

  renderNavButtons() {
    return (
      <Nav className="topbar" style={{paddingLeft: '14px', height: '53px'}}>
        <StickButton position="leftSide" className="zocial GeriDon" style={{fontSize: 'x-small'}}
                     label="Geri" title="Önceki Sayfaya Git"
                     onClick={() => this.props.history.push({
                       pathname: `/${this.props.peykName}/formsession/` + this.props.location.session.id
                     })}/>
        <StickButton position="leftSide" className="zocial Gonder" style={{fontSize: 'x-small'}}
                     label="Tekrar Gönder" title="Zaman Aşımına Uğramış Dokümanları Tekrar Gönder"
                     onClick={() => {
                       this.setState({
                         alert: (
                           <SweetAlert showCancel warning
                                       confirmBtnText="Evet"
                                       cancelBtnText="Hayır"
                                       confirmBtnBsStyle="success"
                                       cancelBtnBsStyle="default"
                                       title="Emin misiniz?"
                                       onConfirm={() => {
                                         this.setState({alert: null});
                                         this.resend()
                                       }}
                                       onCancel={() => this.setState({alert: null})}
                           >
                             Dokümanlar tekrar gönderilecek.
                           </SweetAlert>
                         )
                       });
                     }}/>
      </Nav>
    );
  }

  renderMimeInputs() {

    return <Container>
      <div className="FormRenk">
        {this.props.location.isKepOwner &&
        <Row sm={6}>
          <Col sm={2} style={{top: '7px'}}>
            Kep Hesabı
          </Col>
          <Col sm={6}>
            <Input type="text" style={{marginTop: '2px'}}
                   value={this.state.kepAccount}
                   disable={true}/>
          </Col>
        </Row>}
        <Row sm={6}>
          <Col sm={2} style={{top: '5px'}}>
            E-posta Konu
          </Col>
          <Col sm={6}>
            <Input type="text" style={{marginTop: '2px'}}
                   value={this.state.mimeTitle}
                   disabled/>
          </Col>
        </Row>
        <Row sm={6}>
          <Col sm={2} style={{top: '25px'}}>
            E-Posta İçerik
          </Col>
          <Col sm={6}>
            <Input type="textarea" rows={5}
                   style={{height: '200px', border: '1px solid #E3E3E3', marginTop: '2px'}}
                   value={this.state.mimeBody}
                   disabled/>
          </Col>
        </Row>
        {!this.props.location.isKepOwner &&
        <Row sm={6}>
          <Col sm={2} style={{top: '25px'}}>
            SMS İçerik
          </Col>
          <Col sm={6}>
            <Input type="textarea" rows={5} disabled={true}
                   style={{
                     height: '200px',
                     border: '1px solid #E3E3E3',
                     marginTop: '2px',
                     padding: '3px 3px'
                   }}
                   value={this.state.smsBody}/>
          </Col>
        </Row>}
      </div>
    </Container>
  }

  renderTableList() {
    let columnList = [
      {
        accessor: [
          {Header: "Adı", accessor: "name"},
          {Header: "Soyadı", accessor: "surname"}
        ],
        Cell: row => (
          <div style={{margin: 0}}>
            {row.employee && row.employee.name}
            <br/>
            {row.employee && row.employee.surname}
          </div>
        )
      },
      {
        accessor: [
          {Header: "Departman", accessor: "department"},
          {Header: "Görev", accessor: "jobTitle"}
        ],
        Cell: row => (
          <div style={{margin: 0}}>
            {row.employee && row.employee.department &&
            <TextOverFlowTooltip text={row.employee.department.name} maxLength={12}/>}
            {row.employee && row.employee.department && <br/>}
            <TextOverFlowTooltip text={row.employee && row.employee.jobTitle} maxLength={12}/>
          </div>
        )
      },
      {
        Header: "TCKN",
        accessor: "tckn",
        Cell: row => (
          <div style={{margin: 0}}>
            {row.employee && <span className="text-success">{row.employee.tckn}</span>}
          </div>
        )
      },
      {
        Header: "Form Durumu",
        accessor: "state",
        width: '200px',
        Cell: row => (
          <div>
            {row.state &&
            <span className={"text-" + DocumentState[row.state].messageType}>{DocumentState[row.state].message}</span>}
            {row.state && DocumentState[row.state].value === 601 && <br/>}
            {row.state && DocumentState[row.state].value === 601 &&
            <span className={"text-" + DocumentState[row.state].messageType}>
                            {"Lütfen, Onay akışı şablonunuz ile seçilen çalışan bilgilerinin uyumluluğunu kontrol ediniz."}
                        </span>}
          </div>
        )
      }
    ];
    let columns = renewListForPrivileges(this, columnList, "accessor", this.props.location.isKepOwner);
        return <Table columns={columns}
                  content={this.state.content}
                  loading={this.state.loading}
                  total={this.state.total}
                  page={this.state.page}
                  manual={this.state.manual}
                  size={this.state.size}
                  options={["5", "10", "20", "50", "100"]}
                  fetchData={this.fetchData}
    />;
  }

  render() {
    return (
      <Row className="baseBackground">
        <Notify ref={this.notifyMimeResend} className="modalAlert"/>
        {this.renderNavButtons()}
        {this.state.alert &&
        <div className="row satirBasi">
          <Notify ref={this.notify}/>
          {this.state.alert}
        </div>}
        <div className="row satirBasi">
          <Col md="12">
            <Card className="card">
              <CardHeader>
                <CardTitle tag="h4">
                  Doküman Tekrar Gönder
                </CardTitle>
              </CardHeader>
              <CardBody>
                {this.props.location.session && this.renderMimeInputs()}
              </CardBody>
              <CardBody>
                {this.state.timeoutList && this.state.timeoutList.length > 0 && this.renderTableList() }
              </CardBody>
            </Card>
          </Col>
        </div>
      </Row>
    );
  }
}

export default FormResendPage;
